
.post-feed .vux-header h1 {
  color: #000;
  height: 44px;
}
.post-feed .weui-cells:after {
  height: 0;
  content: "" !important;
}
.post-feed .weui-cell:before {
  right: 15px;
}
.post-feed .vux-no-group-title {
  margin-top: 0;
}
.post-feed .vux-header .vux-header-left a.vux-header-back {
  color: #59b6d7;
  padding-left: 0;
}
.post-feed .left-arrow {
  display: none;
}
.demo-upload-list {
  display: inline-block;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  margin-right: 4px;
}
.demo-upload-list img {
  width: 100%;
  height: 100%;
}
.demo-upload-list-cover {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
}
.demo-upload-list:hover .demo-upload-list-cover {
  display: block;
}
.demo-upload-list-cover i {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin: 0 2px;
}



